.popup__content {
    background-color: #fff;
    width: 430px;
    border-radius: 10px;
    box-sizing: border-box;
    padding: 34px 36px;
    position: relative;
}


@media screen and (max-width: 568px) {
    .popup__content{
        padding: 25px 22px;
        width: 282px;
    }
}